	import Validator from '../../common/validator.esm.js';
	const validator = new Validator();
export default {
	data() {
		return {
			isget:true,
			sumstring: [],
			postData: [],
			flag: false,
			check: "check",
			ischeck: false,
			code: "",
			selectname: [],
			// counttype: "请选择服务类型",
			name: "",
			worktime: "",
			phonenum: "获取手机号码",
			list: "list",
			rSelect: [],
			typefy: "",
			typename: [],
			height: "",
			background: {
				backgroundImage: "linear-gradient(90deg, #54C3F1, #00ABEB)",
			},
			longtype:{
				longjzbj:"../../static/settlement/longjzbj.png",
				longgqty:"../../static/settlement/longgqty.png",
				longgyby:"../../static/settlement/longgqby.png",
				longjdwx:"../../static/settlement/longjdwx.png",
			}
		}
	},
	computed: {
		counttype() {
			let selectname = this.selectname;
			this.sumstring = this.selectname;
			return this.typefy + "," + this.sumstring
		}
	},
	onLoad(option) {
		let that = this;
		console.log(option.id); //打印出上个页面传递的参数。
		let latitude = uni.getStorageSync("latitude");
		console.log(latitude);
		this.typefy = option.id;
		let typedata = uni.getStorageSync("typedata");
		for (var typename in typedata) { //遍历对象属性名
			if (option.id == typename) {
				this.typename = typedata[typename];
				console.log(this.typename);
			}
		};
		uni.login({
			provider: 'weixin',
			success(res) {
				// console.log(res.code);
				that.code = res.code
			}
		})
	},
	methods: {
		//选择二级分类服务类型
		selecttype(e) {
			if (this.rSelect.indexOf(e) == -1) {
				console.log(e) //打印下标
				this.rSelect.push(e); //选中添加到数组里
				this.selectname.push(this.typename[e]);
			} else {
				this.rSelect.splice(this.rSelect.indexOf(e), 1); //取消
				this.selectname.splice(this.typename.indexOf(e), 1);
			}
		},
		//获取手机号码
		getPhoneNumber(e) {
			
			if(this.isget){
				this.isget =false;
				let iv = e.detail.iv,
				encryptedData = e.detail.encryptedData,
				code = this.code,
				that = this;
				//检查session——key是否过期
				uni.checkSession({
					success(res) {
						console.log(res);
						uni.request({
							url: "https://applet.51tiaoyin.com/public/applet/user/get_phone",
							method: 'GET',
							dataType: JSON,
							data: {
								"code": code,
								"iv": iv,
								"encryptedData": encryptedData
							},
							success(res) {
								console.log(res.data);
								let data = JSON.parse(res.data);
								console.log(data);
								if (data.code == 200) {
									// console.log(this);
									console.log(data.data);
									that.phonenum = data.data;
								} else {
									uni.showToast({
										title: "没有获取手机号",
										duration: 1500
									})
								}
							},
							fail(res) {
								console.log(res)
							}
						})
					},
					fail(err) {
						// session_key 已经失效，需要重新执行登录流程
						uni.login({
							success: res => {
								that.data.code = res.code
							}
						})
					}
				})
			}else{
				uni.showToast({
					title:"手机号码已存在"
				})
			}
			// console.log(e.detail.errMsg);
			// console.log(e.detail.iv);
			// console.log(e.detail.encryptedData);
			

			// 	//-----------------是否授权，授权通过进入主页面，授权拒绝则停留在登陆界面
			if (e.detail.errMsg == 'getPhoneNumber:user deny') { //用户点击拒绝
				uni.showToast({
					title: "拒绝获取手机号码"
				})
			} else { //允许授权执行跳转
				uni.showToast({
					title: "正在获取手机号码"
				})
			}
		},
		//选中协议
		checked() {
			return this.flag = !this.flag
		},
		//表单提交
		submit() {
			let name = true,
				phone = true,
				worktime = true,
				counttype = true;
			validator
				.init()
				.add(this.name, [{
						type: 'required',
						message: '用户名不能为空'
					},
					{
						type: 'min:2',
						message: '用户名长度不能小于2位!'
					},
					{
						type: 'max:6',
						message: '用户名长度不能大于6位!'
					}
				])
				.add(this.worktime, [{
						type: 'required',
						message: '手机号码不能为空'
					},
					{
						type: 'max:2',
						message: '工龄必须输入数字',
						callback :(value, message) =>  {
							var reg = '\d{2}';
						      return reg.test(value) ? void 0 : message 
						    }
					}
				])
				.add(this.phone, [{
						type: 'required',
						message: '手机号码不能为空'
					}
				])
				.add(this.counttype, [{
						type: 'required',
						message: '服务类型不能为空'
					},
				])
			const errorMsg = validator.validation()
			if (errorMsg) {
				uni.showToast({
					title:errorMsg
				});
				// console.error(errorMsg)
			} else if(!this.flag){
				uni.showToast({
					title:"请同意协议"
				});
			}
			else {
				uni.showLoading({
					title:"提交中"
				})
				//获取首页中缓存的经纬度,uid
				let latitude = uni.getStorageSync("latitude");
				let longitude = uni.getStorageSync("longitude");
				let uuser_uid = uni.getStorageSync("user_uid");
				console.log(uuser_uid);
				uni.request({
					url: "https://applet.51tiaoyin.com/public/applet/user/get_info",
					method: "POST",
					data: {
						name: this.name,
						age: this.worktime,
						phone: this.phonenum,
						coord: latitude + "," + longitude,
						type: this.counttype,
						uid:uuser_uid
					},
					success(res) {
						// console.log(res);
						//提交成功后的跳转到首页
						uni.hideLoading({
							title:"注册成功"
						});
						setTimeout(()=>{
							uni.reLaunch({
								url:"../index/index"
							})
						},2500)
						
					},
					fail() {
						console.log("失败：" + res);
					}
				})
			}			
		}
	},
	onReady() {

	}
}
